<template>
  <div class="registerSuccess">
    <!--导航内容开始-->
    <header>
      <div class="navigation container clearfix">
        <div class="pull-left">
          <router-link class="logo" to="/"><img src="static/images/index/logo_02.png" alt="" /></router-link>
          <span>{{ $t('register.nav.title') }}</span>
        </div>
        <div class="menu2 pull-right">
          <span class="icon-back"></span>&nbsp;<router-link to="/" class="back-index">{{ $t('register.des.part3') }}</router-link>
        </div>
      </div>
    </header>
		<!--导航内容结束-->

    <div class="container success">
			<div class="clearfix des">
				<div class="pull-left"><img src="static/images/login_register/success.png" /></div>
				
				<div class="pull-left">
					<h3>{{ $t('registerSuccess.des.regStatu') }}</h3>
					<p>{{ $t('registerSuccess.des.desDeatil') }}</p>
				</div>
			</div>
			
			<el-row>
        <el-col :span="12">
          <router-link to="/user/centification">
            <img src="static/images/login_register/centification.png" alt="" />
            <h3>{{ $t('registerSuccess.step.centification.title') }}</h3>
            <p>{{ $t('registerSuccess.step.centification.des') }}</p>
          </router-link>
        </el-col>
        <el-col :span="12">
          <router-link to="/user/account">
            <img src="static/images/login_register/set_password.png" alt="" />
            <h3>{{ $t('registerSuccess.step.setPwd.title') }}</h3>
            <p>{{ $t('registerSuccess.step.setPwd.des') }}</p>
          </router-link>
        </el-col>
      </el-row>
      <el-button class="enter-btn" type="primary" @click="handleEnterIndex">{{ $t('registerSuccess.toIndex') }}</el-button>
    </div>
    <footer>
      <div class="">
        {{ $t('register.footer') }}
      </div>
    </footer>
  </div>
</template>
<script>
  export default {
    data() {
      return {
      }
    },
    methods: {
      handleEnterIndex() {
        this.$router.push('/')
      }
    }
  }
</script>
<style lang="less" scoped>
  .registerSuccess{
    header {
      background: white;
      box-shadow: 0 1px 15px 0px #f3f3f4;
      width: 100%;
      height: 90px;
    }
    .navigation {
      color: white;
      line-height: 90px;
    }
    .navigation .menu2 {
      color: #2f3032;
      font-size: 12px;
    }
    .navigation .menu2 a {
      color: #2f3032;
    }
    .navigation .menu2 .menu2-login {
      font-size: 12px;
      color: #9e82dd;
      padding: 0 10px 0 7px;
    }
    .navigation > .pull-left > span {
      color: #2f3032;
      font-size: 18px;
      display: inline-block;
      margin-left: 20px;
      padding-left: 14px;
      border-left: 1px solid #c7cbd1;
      height: 20px;
      line-height: 20px;
      margin-top: 40px;
    }
    .logo img {
      margin: 18px 10px 24px 0;
      vertical-align: middle;
      border: 0;
    }
    .navigation .pull-left span{
      color: #2f3032;
      font-size: 18px;
      display: inline-block;
      margin-left: 20px;
      padding-left: 10px;
      border-left: 1px solid #c7cbd1;
      height: 20px;
      line-height: 20px;
      margin-top: 40px;
    }
    footer {
      text-align: center;
      color: #94969b;
      font-size: 12px;
      border-top: 1px solid #f0f0f4;
      padding-top: 35px;
      margin-top: 70px;
    }
    .success{
      background: white;
      margin-top: 35px;
      text-align: center;
      box-shadow: 0 1px 15px 0px #f3f3f4;
      .des{
        width: 340px;
        margin: 0 auto;
        padding-top: 85px;
        text-align: left;
        img{
          margin-right: 15px;
        }
      }
      .des h3{
        font-size: 30px;
        font-weight: normal;
        margin-top: 20px;
        margin-bottom: 10px;
        color: rgb(62, 69, 88);
      }
      .des p{
        color: #9397a0;
        font-size: 14px;
        margin-bottom: 10px;
      }
      .el-row{
        width: 920px;
        text-align: center;
        margin: 0 auto;
        padding: 110px 0 70px 0;
        border-bottom: 1px dashed #e6eaf0;
        .el-col-12{
          padding: 0 40px;
          display: block;
          cursor: pointer;
          h3{
            font-weight: normal;
            font-size: 18px;
            color: #2f3032;
            margin-top: 20px;
            margin-bottom: 10px;
          }
          p{
            font-size: 14px;
            color: #a1a5ad;
          }
        }
      }
      .enter-btn{
        margin-bottom: 75px;
        margin-top: 45px;
        width: 155px;
        height: 50px;
        font-size: 16px;
      }
    }
 }
</style>




